.banner{
    background-image: url(../images/banner.png);
    height: 840px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}
.banner-content {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
    font-size: 2.25rem;
    font-weight: bold;
    color: #650300;
    height: 100%;
}
@media (max-width: 992px) {
    .banner-content p {
        font-size: 1.25rem;
    }
}
@media (max-width: 767px) {
    .banner {
        background-image: url(../images/banner-sm.png);
        height: 394px;
    }
    .banner-content p {
        font-size: 1rem;
    }
}

.about {
    background-color: #AA06010D;
    padding-bottom: 5rem;
}
.about-list li { 
    width: 25%;
    padding-right: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.about-list img {
    padding-top: 1.25rem;
    margin-bottom: 2.5rem;
}
.about-list h3 {
    font-family: "noto-bold";
    margin-bottom: 0.25rem;
    font-size: 1.5rem;
    color: #650300;
    font-weight: bolder;
}
.about-list p {
    padding: 0 37px;
}
@media (max-width: 992px) {
    .about-list {
        flex-wrap: wrap;
    }
    .about-list li {
        width: 50%;
        padding-bottom: 0.75rem;
    }
}
@media (max-width: 767px) {
    .about-list li {
        width: 100%;
        padding-bottom: 0.5rem;
    }
}

.contact {
    background-color: #AA06010D;
    padding-bottom: 5rem;
}
.contact-in {
    width: 66%;
    margin: 0 auto;
}
.contact-in p {
    font-size: 1.25rem;
    font-weight: bolder;
    margin-bottom: 1.25rem;
}
.form {
    margin-bottom: 1rem;
}
.form input, textarea {
    display: block;
    background-color: transparent;
    width: 100%;
    padding: 1rem;
    border: 1px solid #000000;
    outline: 0;
}
.button {
    justify-content: center;
    align-items: center;
    padding-top: 2.625rem;
}
.button a {
    display: block;
    width: 180px;
    height: 54px;
    background: #AA0601;
    color: #fff;
    text-align: center;
    padding-top: 1rem;
}
@media (max-width: 992px) {
    .contact-in {
        width: 72.76%;
    }
}
@media (max-width: 767px) {
    .contact-in {
        width: 92.89%;
    }
}

.title {
    font-family: "noto-bold";
    font-size: 3rem;
    padding-top: 4rem;
    padding-bottom: 3.75rem;
    color: #650300;
    font-weight: bolder;
    text-align: center;
}
@media (max-width: 992px) {
    .title {
      font-size: 2rem;
      padding-top: 2.5rem;
      padding-bottom: 2rem;
    }
}
@media (max-width: 767px) {
    .title {
      font-size: 1.5rem;
      padding-top: 1.5rem;
      padding-bottom: 1rem;
    }
}